import axios from 'axios';
import React, { useState, useEffect } from 'react';
import { useHistory, useParams } from 'react-router';
import styled from 'styled-components';
import './Detail.scss'

const Box = styled.div`
    padding: 20px;
`;

const H4 = styled.h4`
    font-size: 25px;
    color: ${props => props.color }
`;

function Info(props) {
    return (
        <p>재고: { props.stock[0] }</p>
    )
}

function Detail(props) {
    const [alert, setalert] = useState(true);
    const [inputData, setinputData] = useState('');
    const { id } = useParams();
    const searchProduct = props.shoes.find((item) => {
        return item.id == id;
    });
    const history = useHistory();

    useEffect(() => {

        const timer = setTimeout(() => {
            setalert(false);
        }, 2000)

        return () => clearTimeout(timer);
    }, [])

    return (
        <div className="container">
            <Box>
                <H4>Detail</H4>
            </Box>

            { inputData }
            <input onChange={(e) => setinputData(e.target.value)} />

            {
                alert === true
                ?  (
                    <div className="my-alert success">
                        <p>재고가 얼마 남지 않았습니다.</p>
                    </div>
                    )
                : null
            }
            
            <div className="row">
                <div className="col-md-6">
                    <img src={`https://jinavely.github.io/shop/shoes${Number(searchProduct.id) + 1}.jpg`} width="100%" />
                </div>
                <div className="col-md-6 mt-4">
                    <h4 className="pt-5">{ searchProduct.title }</h4>
                    <p>{ searchProduct.content }</p>
                    <p>{ searchProduct.price }</p>
                    <Info stock={props.stock} />
                    <button className="btn btn-danger" onClick={() => {  }}>주문하기</button>&nbsp;
                    <button className="btn btn-danger" onClick={() => { history.push('/') }}>뒤로가기</button> 
                </div>
            </div>
        </div>
    )
}

export default Detail;