import React, { useState } from 'react';
import { Navbar, Nav, NavDropdown, Button, Jumbotron, Container, Row, Col } from 'react-bootstrap';
import { Link, Route, Switch } from 'react-router-dom';
import axios from 'axios';
import './App.scss';
import Data from './data';
import Detail from './Detail';

function Cards(props) {
  return (
    <Col xs={4}>
      <Link to={`/detail/${props.i}`}><img src={`https://jinavely.github.io/shop/assets/shoes${(props.i + 1)}.jpg`} width="100%" alt="" /></Link>
      <h4>{ props.shoes.title }</h4>
      <p>{ props.shoes.content } &nbsp; { props.shoes.price }</p>
    </Col>
  )
}

function App() {

  const [shoes, setshoes] = useState(Data);
  const [stock, setstock] = useState([10, 11, 12]);

  return (
    <div className="App">
      
      <Navbar bg="light" expand="lg" className="">
        <Navbar.Brand href="/">ShoeShop</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="ml-auto">
            <Nav.Link as={Link} to="/">Home</Nav.Link>
            <Nav.Link as={Link} to="/detail/0">Detail</Nav.Link>
            <NavDropdown title="Dropdown" id="basic-nav-dropdown">
              <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
            </NavDropdown>
          </Nav>
        </Navbar.Collapse>
      </Navbar>

      <Switch>
        <Route exact path="/">
          <Jumbotron className="background">
            <h1>20% season OFF</h1>
            <p>
              This is a simple hero unit, a simple jumbotron-style component for calling
              extra attention to featured content or information.
            </p>
            <p>
              <Button variant="primary">Learn more</Button>
            </p>
          </Jumbotron>

          <Container>
            <Row>
              {
                shoes.map((item, i) => {
                  return <Cards shoes={shoes[i]} i={i} key={i} />
                })
              }
            </Row>
            <button className="btn btn-primary" 
              onClick={() => {
                axios.get('https://codingapple1.github.io/shop/data2.json')
                  .then((result) => {
                    
                    setshoes([...shoes, ...result.data]);
                  })
                  .catch(() => {
                    console.log('실패했어요');
                  })
              }}
            >더보기</button>
          </Container>
        </Route>

        <Route path="/detail/:id">
          <Detail shoes={shoes} stock={stock} setstock={setstock} />
        </Route>

        <Route path="/:id">
          <Detail />
        </Route>
        {/* <Route path="/어쩌구" component={Modal}></Route> */}
      </Switch>
    </div>
  );
}

export default App;
